import { gsap, Power3 } from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"
import { SplitText } from "gsap/SplitText"

gsap.registerPlugin(ScrollTrigger, SplitText)

export default class animation {
    constructor() {


        const splitLines = new SplitText('#split-me', { 
            type: "lines", 
            linesClass: "line line++" 
        });


        $('#split-me .line').wrap('<div class="line-wrapper">');

        gsap.from( splitLines.lines, 2.8, { 
            y: '200%', 
            ease: Power3.easeOut
            // ease: "Expo.easeInOut",  
        }, 4.2, splitRevert );

        function splitRevert() {
        $('#split-me .line').unwrap();
        splitLines.revert()
        }



        gsap.from(".network", 1.8, {
            opacity: 0,
            delay: 4.9,
        });




        gsap.from(".box__img__1", 1.8, {
            // opacity: 0,
            y: "100%",
            ease: "Expo.easeInOut",
            delay: 1.5,
        });

        gsap.to(".box__img__2", 1.8, {
            // opacity: 0,
            y: "-100%",
            ease: "Expo.easeInOut",
            delay: 2.8,
        });

        gsap.to(".header__img", 1.8, {
            opacity: 0.2,
            delay: 3.9,
        });

    }
}